function filterData(data){
    const maxTempYear = data.filter(
        item => item.name === 'JahreshÃ¶chsttemperatur'
    );

    visualiseChart(maxTempYear);

}

function visualiseChart (data) {
    var margin = {top:10, right: 30, bottom: 30, left: 60},
    width = 460 - margin.left - margin.right,
    height = 400 - margin.top - margin.bottom;

    var svg = d3.select("#visualisationContainer")
                .append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .append("g")
                .attr("transform", `translate(${margin.left}, ${margin.top})`);

    var xAxis = d3.scaleLinear()
                    .domain(d3.extent(data, item => Number(item.jahr)))
                    .range([0, width]);
    
                    

    svg.append("g")
        .attr("transform", `translate(0, ${height})`)
        .call(d3.axisBottom(xAxis));
    
    var yAxis = d3.scaleLinear()
        .domain([(d3.min(data, item => Number(item.wert)))-1, d3.max(data, item => Number(item.wert))])
        .range([height, 0]);
    
    svg.append("g")
        .call(d3.axisLeft(yAxis));
    

    var curve = svg.append("path")
                    .datum(data)
                    .attr("fill", "none")
                    .attr("stroke", "turquoise")
                    .attr("stroke-width", 3)
                    .attr("d", d3.line()
                        .x(item => xAxis(Number(item.jahr)) )
                        .y(item => yAxis (Number(item.wert)))
                    );

    d3.select("#mySlider").on("change", function(d){
        selectedValue = this.value
        xAxis.domain([selectedValue, d3.max(data, item => Number(item.jahr))])
            .range([0, width]);
        
        svg.select("g")
            .attr("transform", `translate(0, ${height})`)
            .call(d3.axisBottom(xAxis));

        curve.datum(data)
            .attr("fill", "none")
            .attr("stroke", "red")
            .attr("stroke-width", 3)
            .attr("d", d3.line()
                .x(item => xAxis(Number(item.jahr)))
                .y(item => yAxis(Number(item.wert)))
            );

    })



}